import axios from 'axios';
import timestamp from 'unix-timestamp';

timestamp.round = true;
const requestContext = {
    sensor: [
        't_coil',
        't_leaving_coil',
        't_leaving_blower',
        't_supply',
        't_return',
        'r_humidity',
    ],
    performance: [
        'rci_Lo',
        'rci_Hi',
        'pue'
    ]
};

function random() {
    let [x, y] = [0, 0];
    while(x === 0) x = Math.random();
    while(y === 0) y = Math.random();
    let number = Math.sqrt( -2.* Math.log( x ) ) * Math.cos( 2.* Math.PI * y );
    number = (number / 10.) + .5;
    if (number > 1 || number < 0) return random();
    return number;
}

export function sendDataToAPI() {
    Object.keys (requestContext).forEach (
        (key) => {
            const payload = {'unix': timestamp.now()};
            requestContext[key].forEach (f => payload[f] = Math.round (
                random() * 1e+04) / 100);

            axios
                .post (`http://sancaliar82.pythonanywhere.com/api/${key}/`, payload)
                .catch (error => console.log('sendDataToAPI', error))
        }
    );
};

const SensorData = () => {
    axios
        .get ('http://sancaliar82.pythonanywhere.com/api/sensor')
        .then (response => console.log (response.data[0]))
};

export default SensorData;