/* *Access Routes */

import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import LocalStorageService from './LocalStorageService';

export function Public ({component: Component, ...rest}) {
    return (
        <Route
            {...rest}
            render = {
                (props) => !LocalStorageService.isAuthenticated() ? <Component {...props}/> :
                    <Redirect to={{pathname: '/smartcabinet/'}}/>
                }/>
    );
};

export function Private ({component: Component, ...rest}) {
    return (
        <Route
            {...rest}
            render = {
                (props) => LocalStorageService.isAuthenticated() ? <Component {...props}/> :
                    <Redirect to={
                        {
                            pathname: '/smartcabinet/auth/',
                            state: {from: props.location}
                        }
                    }/>
                }/>
    );
};