/* Main.Js */

import React, { Component } from 'react';
import { Switch } from 'react-router-dom';
import timestamp from 'unix-timestamp';

import { sendDataToAPI } from './assets/js/Request';
import { ApiContext } from './assets/js/contexts/ApiContext';
import { Private } from './assets/js/utils/AccessRoute';

import 'bootstrap';
import 'startbootstrap-sb-admin-2/css/sb-admin-2.min.css';
import 'startbootstrap-sb-admin-2/vendor/fontawesome-free/css/all.min.css';

import './assets/css/custom.css';
import './assets/css/navigation.css';

import Sidebar from './components/sc-sidebar-alt';
import Topbar from './components/sc-topbar-alt';
import Footer from './components/sc-footer';

import Dashboard from './views/dashboard';
import Error from './views/error';
 
class Main extends Component {
    static contextType = ApiContext;

    constructor(props) {
        super(props);
        this.state = {
            toggle: true,
            response: {
                sensor: null,
                performance: null
            }
        };
        
        this.toggle = this.toggle.bind(this);
        timestamp.round = true;
    };

    toggle(event) {
        event.preventDefault();
        this.setState(
            {toggle: !this.state.toggle}
        );
    };

    componentWillMount() {
        this.setState(
            {response: this.context.getRequest()}
        );
    };

    componentDidMount() {
        let [sendPeriode, getPeriode] = [30, 10];
        let [Tsend, Tget] = [
            sendPeriode + timestamp.now(),
            getPeriode + timestamp.now(),
        ];

        this.loop = setInterval(
            () => {
                let Tnow = timestamp.now();
                if (Tnow > Tsend) {
                    sendDataToAPI(); Tsend += sendPeriode;
                }
                if (Tnow > Tget) {
                    Tget += getPeriode;
                    this.setState(
                        {response: this.context.getRequest()}
                    );
                }
            }, 1000
        );
    };

    componentWillUnmount() {
        Object.keys(this.state.response).forEach(
            (key) => {
                localStorage.removeItem(key)
            }
        );
        clearInterval(this.loop);
    };

    render() {
        return (
            <div id="content-wrapper" className="d-flex flex-column">
                <div id="content" className={`sb-nav-fixed ${this.state.toggle ? null : "sb-sidenav-toggled"}`}>
                    <Topbar func={this.toggle}/>
                    
                    <div id="layoutSidenav">
                        <Sidebar/>
                        <div id="layoutSidenav_content">
                            <Switch>
                                <Private path="/smartcabinet/" component={Dashboard} />
                                <Private path="/smartcabinet/help/" component={Error} />
                            </Switch>
                            <Footer />
                        </div>
                    </div>
                    
                </div> 
            </div>
        );
    };
};

export default Main;
