import axios from 'axios';
import React, { createContext } from 'react';
import { useHistory } from 'react-router-dom';
import LocalStorageService from '../utils/LocalStorageService';

export const ApiContext = createContext();
function ApiProvider(props) {

    const history = useHistory();
    const contextHeader = {
        url: {
            auth: 'http://sancaliar82.pythonanywhere.com/auth/obtain-token',
            refresh: 'http://sancaliar82.pythonanywhere.com/auth/obtain-token/refresh',
        },
        apiKeys: ['sensor', 'performance'],
        path: '/smartcabinet/auth/'
    };

    function eject() {LocalStorageService.clear();
        history.push(contextHeader.path);
    };
    
    axios.interceptors.request.use(
        config => {
            const accessToken = LocalStorageService.getAccessToken();
            if (accessToken) config.headers['Authorization'] = `Bearer ${accessToken}`;
            return config;
        },
        error => Promise.reject(error)
    );
    
    axios.interceptors.response.use(
        response => {return response},
        async function (error) {
            const originalRequest = error.config;
            
            if (originalRequest.url === contextHeader.url.refresh
                && error.response.status === 401) {
                eject();
            };
    
            if (error.response.status === 401 && !originalRequest._retry) {
                originalRequest._retry = true;
    
                return axios
                    .post (
                        contextHeader.url.refresh,
                        {'refresh': LocalStorageService.getRefreshToken()}
                    )
                    .then (response => {
                        if (response.status === 200) {
                            LocalStorageService.setAccessToken(response.data.access);
                            axios.defaults.headers.common['Authorization'] = `Bearer ${
                                response.data.access}`;

                            return axios(originalRequest);
                        } else eject();
                    })
                    .catch (error => {console.log(error);
                        eject();
                    })
            };
            return Promise.reject(error);
        });

    const contextObject = {
        logout: eject,
        getRequest:() => {
            const respJson = {};
            contextHeader.apiKeys.forEach (
                (key) => {
                    axios
                        .get (`http://sancaliar82.pythonanywhere.com/api/${key}/`)
                        .then (response => {
                            const stringJson = JSON.stringify(response.data[0]);
                            LocalStorageService.setItem(
                                key,
                                stringJson
                            );
                            respJson[key] = response.data[0];
                        })
                        .catch (error => console.log(error))
                }
            );
            return respJson;
        },
        response:() => {
            const respData = [];
            contextHeader.apiKeys.forEach(
                (key) => {
                    const json = LocalStorageService.getItem(key);
                    respData.push(json);
                }
            );
            return respData;
        }
    };

    return (
        <ApiContext.Provider value={contextObject}>
            {props.children}
        </ApiContext.Provider>
    );
};

export default ApiProvider;